@use 'sass:map';

*, *:before, *:after {
	box-sizing: border-box;
}

body {
	background-color: color('grey-dark');
}

.site-branding,
article.page {
	background-color: color('green-dark');
}

.hero {

	max-width: bp(xl);
	margin: 0 auto;
	position: relative;
	flex-wrap: no-wrap;

	.wp-block-gallery {
		position: relative;
		left: 50%;
		transform: translate3d(-50%, 0, 0);
		margin-left: 0;
		margin-right: 0;
		flex-wrap: nowrap;
	}

	p {
		text-align: center;
		color: color(green-light);
		&:first-of-type {
			font-size: fs(h5);
		}
	}
}

.is-layout-constrained {
	max-width: bp(lg);
	margin: 0 auto;
	padding: spacing(md);
	padding-top: spacing(xs);
	@media (min-width: bp(md)) {
		padding-top: 0;
		padding-bottom: spacing(md);
		padding-left: 0;
		padding-right: 0;
	}
}

#amplify,
#drive,
#support {
	color: color(white);

	.is-layout-constrained {
		padding-top: spacing(md);
	}
}




.svg-sprite-wrapper {
    overflow: hidden;
    position: relative;
    display: inline-block;

    a,
    div,
    svg {
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }
}
