.defenders {
	padding-bottom: 0;
}

.wp-block-site-functionality-defender {

	background-repeat: no-repeat;
	background-size: cover;
	position: relative;
	padding-bottom: spacing(md);

	&::after {
		content: '';
		top: 0;
		left: 0;
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.35);
	}

	> .wp-block-group {
		display: flex;
		justify-content: space-between;
		max-width: bp(xl);
		margin: 0 auto;
		z-index: 10;
	}

	.wp-social-link {
		span {
			color: color(grey-dark);
		}
		svg path {
			fill: color(grey-dark);
		}
	}

	.row {
		width: 100%;
		display: grid;
		@media screen and (min-width: bp(sm)) {
			flex-direction: row;
			display: flex;
			justify-content: space-between;
			padding: spacing(md);
		}
	}

	.defender__profile {
		z-index: 10;
		box-sizing: border-box;
		background-color: color('white');
		color: color(grey-dark);
		grid-row: 1;
		padding: spacing(sm);
		figure {
			margin-bottom: 0.5rem;
		}

		@media screen and (min-width: bp(md)) {
			width: 300px;
			padding: spacing(md);
		}
	}

	.defender__forest {
		z-index: 10;
		background-color: color('grey-dark');
		color: color('white');
		grid-row: 2;
		padding: spacing(sm);
		figure {
			margin-bottom: 0.5rem;
		}
		@media (min-width: bp(md)) {
			padding: spacing(md);
			max-width: 33.4875rem;
		}

	}
}
